*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Lato", sans-serif;
  box-sizing: border-box;
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
}

.container {
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
}

.section__header {
  text-align: center;
  margin: 5rem 0;
}

.heading-h2 {
  font-size: clamp(2.6rem, 5vw, 4rem);
}

.p-line {
  font-size: clamp(1.8rem, 1vw, 2rem);
}

@media only screen and (min-width: 64em) {
  .col-2 {
    flex: 0 1 calc(50% - 1.5rem);
  }
}

.header {
  background-image: linear-gradient(90deg, rgba(213, 32, 71, 0.8) 0%, rgba(238, 61, 27, 0.8) 100%), url(/img/hero.jpg);
  background-size: cover;
}
.header__text {
  text-align: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-block: 15%;
}
.header__text h1 {
  font-size: clamp(3rem, 7vw, 8rem);
  text-transform: uppercase;
  transition: all 250ms ease;
  margin: 0;
}
.header__text p {
  max-width: 52rem;
  font-size: clamp(1.6rem, 2vw, 2rem);
}

.navbar {
  font-size: 1.4rem;
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  padding-top: 5rem;
  padding-bottom: 1.7rem;
  transition: transform 1s;
  justify-content: space-between;
  align-items: center;
}
.navbar__logo a {
  font-size: 1.6rem;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar__logo img {
  width: 3rem;
}
.navbar__logo p {
  text-transform: uppercase;
  font-weight: 700;
  margin-left: 0.652rem;
}
.navbar__menu {
  font-weight: 400;
  display: none;
  align-items: center;
  justify-content: center;
}
@media only screen and (min-width: 64em) {
  .navbar__menu {
    display: flex;
  }
}
.navbar__menu.openMenuMobile {
  background: rgba(19, 19, 19, 0.8);
  transform: translateY(0);
  opacity: 1;
  transition: all 0.5s ease;
}
.navbar__menu li {
  margin: 1rem;
}
.navbar__menu li a {
  color: #fff;
  text-transform: uppercase;
}
@media only screen and (min-width: 64em) {
  .navbar__menu li:nth-child(6) {
    display: none;
  }
}
.navbar__location {
  display: none;
  font-weight: 400;
}
@media only screen and (min-width: 64em) {
  .navbar__location {
    display: flex;
  }
}
.navbar__location a {
  color: #fff;
  text-transform: uppercase;
}
.navbar__hamburger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 1.9rem;
  cursor: pointer;
}
@media only screen and (min-width: 64em) {
  .navbar__hamburger {
    display: none;
  }
}
.navbar__hamburger span {
  width: 2rem;
  height: 0.3rem;
  background: #fff;
}
.navbar__hamburger.active span:nth-child(1) {
  margin: 8px auto;
  transform: translateY(13px) rotate(45deg);
}
.navbar__hamburger.active span:nth-child(2) {
  opacity: 0;
}
.navbar__hamburger.active span:nth-child(3) {
  margin: 8px auto;
  transform: translateY(-13px) rotate(-45deg);
}

.manage {
  padding: 8rem 0;
}
.manage .d-flex-layout {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.manage__text {
  padding-bottom: 6rem;
}
@media only screen and (min-width: 64em) {
  .manage__text {
    padding-right: 8rem;
  }
}
.manage__video {
  position: relative;
}
.manage__video img {
  border-radius: 2rem;
}
.manage__video-play {
  position: absolute;
  font-size: 2rem;
  cursor: pointer;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media only screen and (min-width: 64em) {
  .manage__video-play {
    top: 50%;
    left: 0;
    transform: translate(-50%, -50%);
  }
}
.manage__video-play .fa-play {
  background: #ed4c5c;
  color: #fff;
  width: 8rem;
  height: 8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5rem;
}
.manage .link {
  text-transform: capitalize;
  color: #ed4c5c;
  display: block;
  font-size: clamp(1.8rem, 1vw, 2rem);
}

.btn {
  padding: 1rem 3rem;
  border-radius: 0.652rem;
  text-transform: uppercase;
  color: #fff;
  font-size: 1.4rem;
}

.btn--bg--white {
  background: #fff;
}

.btn--text--colored {
  color: #ed4c5c;
}